import React from 'react';

const Datatable =(props)=>{
    
    return (
            <>
                <tr>
                <th scope="row">{props.row}</th>
                <td>{props.element}</td>
                <td><button type="button" className="text-primary" onClick={props.editData}><i className="fa fa-edit"></i>Edit</button></td>
                <td><button type="button" className="text-danger" onClick={props.deleteData}><i className="fa fa-trash"></i>Delete</button></td>
                </tr>
            </>
      )
}

export default Datatable