import React,{useState, useEffect} from 'react';
import Datatable from './Datatable';

// to set data localstorage

const getLocalItems =()=>{
	let list = localStorage.getItem('list');

	if (list){
		return JSON.parse(localStorage.getItem('list'));
	}else{
		return[];
	}
} 

const OuterToDo =(props)=>{
	const [inputData, setInputData] = useState('');
	const [items, setItems] = useState(getLocalItems());
	const [toggleSubmit, setToggleSubmit] = useState(true);
	const [isEditItem, setIsEditItem] = useState(null);
	



	const userInputVal =(e)=>{
		setInputData(e.target.value);
	}
	const addItem=()=>{
		if(!inputData){
		  alert('Enter Your Todotask!!')
		}else if(inputData && !toggleSubmit) {
			setItems(items.map((elem)=>{
				if(elem.id === isEditItem){
					return {...elem, name:inputData}
				}
				return elem;
			}))
			setToggleSubmit(true);
			setInputData('');
			setIsEditItem(null);
		}else{
			const allInputData = {id:new Date().getTime().toString(), name:inputData}
			setItems([...items, allInputData]);
			setInputData('');
		}
		
	}

	const editItem =(id)=>{
		let newEditItem = items.find((elem)=>{
			return elem.id === id
		});

		setToggleSubmit(false);
		setInputData(newEditItem.name);
		setIsEditItem(id);
	}

	const deleteItem =(id)=>{
		const updateditems = items.filter((elem)=>{
		 	return id !== elem.id;
		});

		setItems(updateditems);
	}

	const removeAll =()=>{
		setItems([]);
	}

	useEffect(()=>{
		localStorage.setItem('list', JSON.stringify(items));
	}, [items]);

	return(
		<section className="todo-outer">
		    <div className="container">
		      <div className="row justify-content-md-center">
		        <div className="col-sm-12 col-md-8">
		          <h1>Welcome in Todo List Application</h1>
		          <div className="todo-inner">
		            <div className="form-row">
		              <div className="col-8 mr-sm-2">
		                <input type="text" className="form-control"  placeholder="Enter your task" value={inputData} onChange={userInputVal}/>
		                <input type="hidden"/>
		              </div>
		              {toggleSubmit? <button type="button" className="btn btn-success mr-sm-2" onClick={addItem}> 
		                Add Task
		              </button>:<button type="button" className="btn btn-success mr-sm-2" onClick={addItem}>
		                Save Task
		              </button>}
		              
		              <button type="button" className="btn btn-danger" onClick={removeAll}>
		                Delete All
		              </button>
		            </div>
			            <div className="to-do-output">
	           			   <table className="table table-striped mt-3 mb-0">
	           			    <tbody>
			           		 {
			            		items.map((elem, index)=>{
			            			return (<><Datatable element={elem.name} row={index+1} key={elem.id} deleteData={()=>deleteItem(elem.id)} editData={()=>editItem(elem.id)}/></>)
			            			
			            		})
			           		 }
			           		 </tbody>
			        	    </table>
	         			</div>
		          </div>
		        </div>
		      </div>
		    </div>
		  </section>	
	)
}

export default OuterToDo